.client {
    
    &-item {
        display: inline-block;
        min-height: 80px;
        line-height: 80px;
        padding-top: 16px;
        padding-bottom: 16px;
        opacity: 0.4;
        transition: all 0.3s ease-in-out;
        &:hover {
            opacity: 1;
        }
    }
}

