.team-item{
	text-align: center;
	
	&-image{
		position: relative;
		overflow: hidden;
		&-overlay{
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: fade(@theme-color,90%);
			top: -100%;
			left: 0;
			transition: all 300ms;
		}
		img{
			max-width: 100%;
		}
	}
	
	&-icons{
		position: absolute;
		padding: 15px 0;
		height: 50px;
		width: 100%;
		top: 50%;
		margin-top: -25px;
		font-size: 15px;
		a{
			color: @white-color;
			margin: 0 15px;
		}
	}
	
	&-info {
	    background: darken(@white-color, 2%);
        box-shadow: 0px 1px 0px fade(@black-color, 10%);
        padding: 15px 0;
	}
	
	&-name{
		font-family: @brand-font;
		font-size: 15px;
	}
	
	&-position{
		font-size:11px;
		color: #aaaaaa;
	}
	
	&:hover{
		.team-item-image-overlay{
			top: 0;
		}
		.team-item-name{
			color: @theme-color;
		}
	}
	
}