.service {
    background-color: @light-background;
    padding: 40px 30px 30px;
    
    &-icon{
        background-color: @theme-color;
        color: @white-color;
        width: 44px;
        height: 44px;
        text-align: center;
        font-size: 23px;
        line-height: 42px;
    }
    
    h4{
        text-transform: uppercase;
		margin-top: 35px;
    }
    
    &-text{
        margin: 10px 0;
    }
    
}