body, html {
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    background: @white-color;
    font-size: 14px;
    font-family: @regular-font;
    color: lighten(@dark-color, 20%);
}

a {
    color: @theme-color;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
            
    &:hover, &:focus, &:active {
        color: darken(@theme-color,15%);
        text-decoration: none;
        outline: 0;
    }
    
}

ul, li {
    list-style:none;
    padding: 0;
}

::-moz-selection  {
	color: #fcfcfc;
	text-shadow:none;
	background: #0f0f0f;
}

::selection {
	color: #fcfcfc;
	text-shadow:none;
	background: #0f0f0f;
}

//Label
.label-theme {
    background-color: @theme-color;
    transition: all 0.3s ease-in-out;
    &:hover {
        background-color: darken(@theme-color, 15%);
    }
    a {
        color: @white-color;
    }
}

//Section
section {
    position: relative;
    width: 100%;
}

//Background
.light-bg {
    background-color: @light-background;
}

// Image background
.image-bg {
    background-position: 50% 0;
    background-repeat: no-repeat;
    background-size: cover;
}

// Parallax background
.parallax-bg {
    position: relative;
    background-attachment: fixed;
    background-position: 50% 0;
    background-repeat: no-repeat;
    background-size: cover;
}

// Full Height
.full-height {
    height: 100vh;
}

// Back To top
#back-to-top {
    position: fixed;
    bottom: 20px;
    right: 40px;
    z-index: 9999;  
    text-align: center;
    cursor: pointer;
    border: 0;
    text-decoration: none;
    opacity: 0;
    
    i {
        width: 32px;
        height: 32px;
        line-height: 32px;
        color: lighten(@dark-color,40%);
        font-size: 20px;
        background: @dark-color;
        transition: all .3s ease-in-out;
        
        &:hover {
            background: @theme-color;
            color: @white-color;
        }
    }
    
    &.show {
        opacity: 1;
    }
}