.stat-item{
    overflow: hidden;
    
    &-icon{
        float: left;
        background-color: @theme-color;
        color: @white-color;
        width: 44px;
        height: 44px;
        text-align: center;
        font-size: 23px;
        line-height: 42px;
        margin-right: 18px;
    }
    
    &-number{
        float: left;
        font-weight: 700;
        font-size: 30px;
        margin-right: 18px;
        line-height: 42px;
        min-width: 52px;
    }
    
    &-text{
        padding-top: 4px;
		padding-left: 62px;
    }

}