//Form
.contacts-form{
	padding-top: 10px;
	&-item{
		margin-bottom: 20px;
		
		input[type="text"], input[type="password"], textarea{
		    background: darken(@white-color, 2%);
			border: none;
			color: @black-color;
			font-size: 0.9em;
			width: 100%;
			padding: 10px 15px;
			&:focus{
                outline: none;
            }
		}
		
		textarea{
			height: 160px;
		}
		
		::-webkit-input-placeholder { /* WebKit, Blink, Edge */
			color: darken(@white-color, 30%);
		}
		:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
		    color: darken(@white-color, 30%);
		    opacity:  1;
		}
		::-moz-placeholder { /* Mozilla Firefox 19+ */
		    color: darken(@white-color, 30%);
		    opacity:  1;
		}
		:-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: darken(@white-color, 30%);
		}
		
		
		
	}
	
	&-button{
		margin-bottom: 0;
	}
	
}

//Information
.contact-info{
    padding-left: 30px;
    
    &-icon{
        float: left;
        background-color: @theme-color;
        color: @white-color;
        width: 40px;
        height: 40px;
        text-align: center;
        font-size: 18px;
        line-height: 40px;
        margin-right: 18px;
    }
    
    &-title{
        font-weight: 700;
        font-size: 16px;
        line-height: 1.2em;
    }
    
    &-text{
        font-size: 0.9em;
        padding-top: 5px;
		padding-left: 58px;
    }

}